"use client";

type Props = {
  /** Optional: array of node labels for a simple left-to-right flow */
  nodes?: string[];
  /** Optional: emoji or icon string per node (1:1 with nodes) */
  icons?: (string | null)[];
  /** Custom content (e.g. description or custom diagram) */
  children?: React.ReactNode;
};

export function FlowDiagram({ nodes, icons, children }: Props) {
  return (
    <div className="glass-card rounded-2xl p-5 overflow-x-auto">
      <h3 className="text-lg font-semibold text-white/90 mb-3">Flow</h3>
      {nodes && nodes.length > 0 ? (
        <div className="flex flex-wrap items-center gap-2 text-sm">
          {nodes.map((node, i) => {
            const icon = icons?.[i];
            return (
              <span key={i} className="flex items-center gap-2">
                <span className="flex items-center gap-1.5 rounded-lg border border-white/20 bg-white/5 px-3 py-1.5 font-medium text-white/90">
                  {icon && <span className="text-base" aria-hidden>{icon}</span>}
                  {node}
                </span>
                {i < nodes.length - 1 && (
                  <span className="text-purple-400/80">→</span>
                )}
              </span>
            );
          })}
        </div>
      ) : (
        <div className="text-sm text-white/70 leading-relaxed">{children}</div>
      )}
    </div>
  );
}
